<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button @click="addUserRoleBtn" type="primary"></el-button>
        </el-col>
      </el-row>
      <el-table :data="roleslist" stripe border>
        <!--  -->
        <el-table-column type="expand">
          <!--  -->
          <template v-slot="scope">
            <el-row :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item, i1) in scope.row.children" :key="item.id">
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeTagId(scope.row, item.id)">
                  {{ item.authName }}
                </el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>

              <!--  -->
              <el-col :span="19">
                <el-row :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']" v-for="(item2, i2) in item.children" :key="item2.id">
                  <!--  -->
                  <el-col :span="6">
                    <el-tag @close="removeTagId(scope.row, item2.id)" closable type="success">{{ item2.authName }}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>

                  <!--  -->
                  <el-col :span="18">
                    <el-tag @close="removeTagId(scope.row, item3.id)" closable type="warning" v-for="item3 in item2.children" :key="item3.id">
                      {{ item3.authName }}
                    </el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>

        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="roleName" label=""> </el-table-column>
        <el-table-column prop="roleDesc" label=""> </el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot="scope">
            <el-button @click="etidRole(scope.row.id)" size="mini" type="primary" icon="el-icon-edit"></el-button>
            <el-button @click="deleteRole(scope.row.id)" size="mini" type="danger" icon="el-icon-delete"></el-button>
            <el-button @click="showSetRightDialog(scope.row)" size="mini" type="warning" icon="el-icon-setting"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog @close="closeRightDialog" title="" :visible.sync="SetRightDialogVisible" width="50%">
      <!-- tree -->
      <el-tree ref="treeRef" :default-checked-keys="defaultTree" node-key="id" default-expand-all show-checkbox :data="roleslisttree" :props="treeProps"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="SetRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog @close="resetDialogRoleForm" title="" :visible.sync="addRoleDialogVisible" width="50%">
      <el-form :model="AddRoleruleForm" ref="AddRoleFormRef" :rules="addRoleRulesForm" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="AddRoleruleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="AddRoleruleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRoleDialogReset"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog @close="editCloseRemove" title="" :visible.sync="editRoleDialogVisible" width="50%">
      <el-form :rules="editRoleFormRules" :model="editRoleruleForm" ref="editRoleFormRef" label-width="80px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editRoleruleForm.roleName"></el-input>
        </el-form-item>

        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editRoleruleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editRoleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'Roles',
  data() {
    return {
      // 
      roleslist: [],
      SetRightDialogVisible: false,
      // tree
      roleslisttree: [],
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // tree
      defaultTree: [],
      // id
      storRoleId: '',
      // 
      addRoleDialogVisible: false,
      // form
      AddRoleruleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      editRoleDialogVisible: false,
      // id
      etidRoleId: '',
      editRoleruleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      addRoleRulesForm: {
        roleName: [{ required: true, message: '', trigger: 'blur' }],
        roleDesc: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      editRoleFormRules: {
        roleName: [{ required: true, message: '', trigger: 'blur' }],
        roleDesc: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('/roles')
      if (res.meta.status !== 200) return
      this.roleslist = res.data
    },
    // roleID ID
    // rghtID ID id
    // 
    async removeTagId(role, rghtId) {
      const removeComfirm = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (removeComfirm !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete(`/roles/${role.id}/rights/${rghtId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // this.getRolesList()
      // console.log(role)
      role.children = res.data
    },
    // tree
    async showSetRightDialog(role) {
      this.storRoleId = role.id
      const { data: res } = await this.$http.get('/rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // data
      this.roleslisttree = res.data

      this.getLeafKyes(role, this.defaultTree)

      // 
      this.SetRightDialogVisible = true
    },

    // pusharr
    getLeafKyes(node, arr) {
      // children 
      // id
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(itme => this.getLeafKyes(itme, arr))
    },
    // defaultTree 
    // treebug
    //  
    closeRightDialog() {
      this.defaultTree = []
    },
    async allotRights() {
      const keys = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
      const keyStr = keys.join(',')

      // axios
      const { data: res } = await this.$http.post(`/roles/${this.storRoleId}/rights`, { rids: keyStr })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
      this.SetRightDialogVisible = false
    },

    // 
    addUserRoleBtn() {
      this.addRoleDialogVisible = true
    },
    async addRoleDialogReset() {
      const { data: res } = await this.$http.post('/roles', { roleName: this.AddRoleruleForm.roleName, roleDesc: this.AddRoleruleForm.roleDesc })

      if (res.meta.status !== 201) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
      this.addRoleDialogVisible = false
    },
    resetDialogRoleForm() {
      this.AddRoleruleForm.roleName = ''
      this.AddRoleruleForm.roleDesc = ''
      this.$refs.AddRoleFormRef.resetFields()
    },
    async etidRole(id) {
      this.etidRoleId = id
      // id
      const { data: res } = await this.$http.get(`/roles/${id}`)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.editRoleruleForm = res.data

      this.editRoleDialogVisible = true
    },
    // 
    async editRoleSubmit() {
      // console.log(this.etidRoleId)
      const { data: res } = await this.$http.put(`/roles/${this.etidRoleId}`, { roleName: this.editRoleruleForm.roleName, roleDesc: this.editRoleruleForm.roleDesc })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
      this.editRoleDialogVisible = false
    },
    editCloseRemove() {
      // 
      this.$refs.editRoleFormRef.resetFields()
      this.etidRoleId = ''
    },
    async deleteRole(id) {
      const confirData = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirData !== 'confirm') {
        return this.$message.info('')
      }

      const { data: res } = await this.$http.delete(`roles/${id}`)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getRolesList()
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 6px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
// 
.vcenter {
  display: flex;
  align-items: center;
}
</style>
