<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <div>
        <div style="margin-top: 15px;">
          <el-row :gutter="20">
            <!--  -->
            <el-col :span="8">
              <el-input @clear="getUserList()" clearable v-model="queryUser.query" placeholder="" class="input-with-select">
                <el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
              </el-input>
            </el-col>
            <el-col :span="4">
              <el-button type="primary" @click="addDialogVisible = true"></el-button>
            </el-col>
          </el-row>
        </div>
      </div>
      <!--  -->
      <el-table stripe border :data="userlist">
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column label="">
          <!--  -->
          <template v-slot:default="scope">
            <!-- {{ scope.row }} -->
            <el-switch v-model="scope.row.mg_state" @change="userStartChanged(scope.row)" active-color="#13ce66" inactive-color="#ff4949"> </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="180">
          <template #default="scope">
            <!--  -->
            <el-button @click="editUser(scope.row.id)" size="mini" type="primary" icon="el-icon-edit"> </el-button>

            <!--  -->
            <el-button @click="deleteIdUser(scope.row.id)" size="mini" type="danger" icon="el-icon-delete"></el-button>
            <!--  -->
            <el-tooltip :enterable="false" class="item" effect="dark" content="" placement="top">
              <el-button @click="setUserRole(scope.row)" size="mini" type="warning" icon="el-icon-setting"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :page-sizes="[2, 5, 10]" :page-size="queryUser.pagesize" :current-page="queryUser.pagenum" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>

      <!--  -->
      <el-dialog @close="resetForm" title="" :visible.sync="addDialogVisible" width="50%">
        <!--  -->
        <el-form :model="addForm" :rules="addForRules" ref="addFormRef" label-width="70px">
          <!--  -->
          <el-form-item label="" prop="username">
            <el-input v-model="addForm.username"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label="" prop="password">
            <el-input v-model="addForm.password"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label="" prop="emali">
            <el-input v-model="addForm.emali"></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label="" prop="mobile">
            <el-input v-model="addForm.mobile"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog @close="resetEditForm" title="" :visible.sync="editDialogVisible" width="50%">
        <!--  form -->
        <el-form :model="editUserdata" :rules="editUserdataRules" ref="editUserdataRef" label-width="70px" class="demo-ruleForm">
          <el-form-item label="">
            <el-input v-model="editUserdata.username" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="email">
            <el-input v-model="editUserdata.email"></el-input>
          </el-form-item>

          <el-form-item label="" prop="mobile">
            <el-input v-model="editUserdata.mobile"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editUserInfo"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog @close="resetDialogRole" title="" :visible.sync="roleDialogVisible" width="50%">
        <div>
          <p>{{ userinfo.username }}</p>
          <p> {{ userinfo.role_name }}</p>
        </div>
        <el-select v-model="selectRoleList" placeholder="">
          <el-option v-for="item in setRoleListData" :key="item.id" :label="item.roleName" :value="item.id"> </el-option>
        </el-select>

        <span slot="footer" class="dialog-footer">
          <el-button @click="roleDialogVisible = false"> </el-button>
          <el-button type="primary" @click="grantRoleDialog"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  name: 'Users',
  data() {
    var checkEmali = (rule, value, cb) => {
      const regEmali = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
      if (regEmali.test(value)) {
        // 
        return cb()
      }
      // 
      cb(new Error(''))
    }
    var checkNumber = (rule, value, cb) => {
      const regNumber = /^1(3[0-9]|4[01456879]|5[0-35-9]|6[2567]|7[0-8]|8[0-9]|9[0-35-9])\d{8}$/
      if (regNumber.test(value)) {
        // 
        return cb()
      }
      // 
      cb(new Error(''))
    }

    return {
      queryUser: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 2
      },
      userlist: [],
      total: 0,
      // 
      addDialogVisible: false,
      addForm: {
        username: '',
        password: '',
        emali: '',
        mobile: ''
      },
      addForRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
        ],
        emali: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmali, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkNumber, trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editUserdata: {},
      editUserdataRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmali, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkNumber, trigger: 'blur' }
        ]
      },

      // 
      roleDialogVisible: false,
      // 
      userinfo: {},
      // 
      setRoleListData: [],
      // id
      selectRoleList: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    // 
    async getUserList() {
      const { data: res } = await this.$http.get('/users', { params: this.queryUser })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      this.userlist = res.data.users
      this.total = res.data.total
    },
    // 
    handleSizeChange(newSize) {
      // console.log(newSize)
      this.queryUser.pagesize = newSize
      this.getUserList()
    },
    // 
    handleCurrentChange(newCurr) {
      this.queryUser.pagenum = newCurr
      this.getUserList()
    },
    // switch 
    async userStartChanged(userinfo) {
      const { data: res } = await this.$http.put(`users/${userinfo.id}/state/${userinfo.mg_state}`)
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    resetForm() {
      this.$refs.addFormRef.resetFields()
    },
    // axios
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('/users', this.addForm)
        // console.log(res.meta.status)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.$message.success('')
        // addUser 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    async editUser(id) {
      const { data: res } = await this.$http.get('/users/' + id)
      if (res.meta.status !== 200) return
      this.editUserdata = res.data
      this.editDialogVisible = true
    },
    // 
    resetEditForm() {
      this.$refs.editUserdataRef.resetFields()
    },
    // 
    editUserInfo() {
      this.$refs.editUserdataRef.validate(async valid => {
        if (!valid) return
        // axios

        const { data: res } = await this.$http.put('/users/' + this.editUserdata.id, { email: this.editUserdata.email, mobile: this.editUserdata.mobile })

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    async deleteIdUser(id) {
      // res confirm
      // res cancel
      const res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => {
        // promise catch
        return err
      })

      if (res !== 'confirm') {
        return this.$message.info('')
      }
      // axios
      const { data: deleteRes } = await this.$http.delete('/users/' + id)
      if (deleteRes.meta.status !== 200) {
        return this.$message.error('')
      }

      this.$message.success('')
      // 
      if (JSON.stringify(deleteRes.data) !== {}) {
        this.getUserList()
      }
      // -1
      this.handleCurrentChange(this.queryUser.pagenum - 1)
      this.getUserList()
    },
    // 
    async setUserRole(userinfo) {
      // data
      this.userinfo = userinfo
      // 
      const { data: res } = await this.$http.get('/roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      // 
      this.setRoleListData = res.data
      // 
      this.roleDialogVisible = true
    },

    // 
    async grantRoleDialog() {
      const { data: res } = await this.$http.put(`users/${this.userinfo.id}/role`, { rid: this.selectRoleList })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      this.getUserList()
      // 
      this.roleDialogVisible = false
    },
    resetDialogRole() {
      this.userinfo = ''
      this.selectRoleList = ''
    }
  }
}
</script>
<style lang="less" scoped></style>
