/* eslint-disable no-unused-expressions */
<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <div class="btn_upload">
        <el-button @click="exportExcel" type="primary"><i class="el-icon-refresh-left el-icon--right"></i></el-button>
      </div>
      <!--  -->
      <el-table id="out-table" :data="rigtslist" border stripe @selection-change="handleSelectionChange">
        <el-table-column type="selection"> </el-table-column>
        <el-table-column type="index" label=""> </el-table-column>
        <el-table-column prop="authName" label=""> </el-table-column>
        <el-table-column prop="path" label=""> </el-table-column>
        <el-table-column prop="level" label="">
          <template v-slot="scope">
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag v-else-if="scope.row.level === '1'" type="success"></el-tag>
            <el-tag v-else type="danger"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
// Excel
import Blob from '@/lib/excel/Blob.js'
// eslint-disable-next-line no-unused-vars
import Export2Excel from '@/lib/excel/Export2Excel.js'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

export default {
  name: 'Rights',
  data() {
    return {
      rigtslist: [],
      multipleSelection: [] // 
    }
  },
  created() {
    this.getRigtsList()
  },
  methods: {
    async getRigtsList() {
      const { data: res } = await this.$http.get('/rights/list')
      if (res.meta.status !== 200) return
      this.rigtslist = res.data
    },
    // 
    handleSelectionChange(val) {
      console.log(val)
      this.multipleSelection = val
    },
    async exportExcel() {
      if (this.multipleSelection.length === 0) {
        this.$message.warning('')
        return
      }
      const confirm = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      if (confirm !== 'confirm') {
        return this.$message.info('')
      }
      this.exportExcel1()
      console.log('')
    },
    exportExcel1() {
      // 
      require.ensure([], () => {
        // eslint-disable-next-line camelcase
        const { export_json_to_excel } = require('@/lib/excel/Export2Excel.js') // Export2Excel
        const tableHeader = ['', '', ''] // Excel
        const filterVal = ['authName', 'path', 'level'] // prop
        const list = this.filterTableData(JSON.parse(JSON.stringify(this.multipleSelection))) // this.selectListfilterTableData()list
        const data = this.formatJson(filterVal, list)
        export_json_to_excel(tableHeader, data, '') // 
      })
    },
    formatJson(filterVal, Data) {
      return Data.map(v => filterVal.map(j => v[j]))
    },
    // 
    filterTableData(data) {
      data.forEach(item => {
        // 
        // item.country ? (item.province = item.province + '-' + item.city + '-' + item.area + '-' + item.country) : (item.province = item.province + '-' + item.city + '-' + item.area)
        // // eslint-disable-next-line no-unused-expressions
        // item.isdel ? (item.isdel = '') : ''
      })
      return data
    },
    // 
    exportExcel2() {
      var wb = XLSX.utils.table_to_book(document.querySelector('#out-table'))
      /*  */
      var wbout = XLSX.write(wb, {
        bookType: 'xlsx',
        bookSST: true,
        type: 'array'
      })

      try {
        FileSaver.saveAs(
          // Blob 
          // Blob JavaScript
          // File Blob blob 
          //  Blob 
          new Blob([wbout], { type: 'application/octet-stream' }),
          // 
          '.xlsx'
        )
      } catch (e) {
        if (typeof console !== 'undefined') console.log(e, wbout)
      }
      return wbout
    }
  }
}
</script>
<style lang="less" scoped>
.btn_upload {
  display: flex;
  justify-content: flex-end;
}
</style>
